
$Blue: hsl(223, 87%, 63%);
$PalBlue: hsl(223, 100%, 88%);
$LighRed: hsl(354, 100%, 66%);
$Gray: hsl(0, 0%, 59%);
$VerDarBlue: hsl(209, 33%, 12%);
@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@300;600;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    font-family: 'Libre Franklin', sans-serif;
    min-height: 100vh;
    overflow: hidden;
    text-align: center;
    background: url("../../public/images/background-image-g0fbbdee3e_1280.png") center/cover; 
    .card {
        max-width: 85%;
        margin: 60px auto 0 auto;
        background-color: white;
        min-height: 85vh;
        h1 {
            padding-top: 30px;
            font-size: 1.3em;
            color: $Blue;
            span {
                color: black;
            }
        }
        h2 {
            color: $Gray;
            margin-top: 20px;
            font-weight: 300;
            font-size: 2em;
            span {
                color: black;
                font-weight: 700;
            }
        }
        p {
            margin-top: 10px;
        }
        form {
          
            margin-top: 20px;
            position: relative;
            #error {
                position: absolute;
                left: 32%;
                transform: translateY(-40%);
                font-size: 0.8em;
                color: $LighRed;
                font-style: italic;
                visibility: hidden;
            }
            #mail {
                padding: 15px;
                width: 280px;
                margin-right: 20px;
                height: 35px;
                border: 1px solid $PalBlue;
                color: $Gray;
                border-radius: 20px;
                font-weight: 600;
            }
            #submit {
                padding: 10px 33px;
                border-radius: 35px;
                border: none;
                background: $Blue;
                color: white;
                -webkit-box-shadow: 1px 5px 14px 2px rgba(0, 0, 0, 0.945); 
                 box-shadow: 1px 5px 14px 2px rgba(0, 0, 0, 0.411);
                 cursor: pointer;
            }
        }

        #img {
            margin-top: 50px;
            width: 65%;
        }
    }
  
}
